// CSS RESET
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}

// Theming
$elevations: (
  "00dp": 0,
  "01dp": 0.05,
  "02dp": 0.07,
  "03dp": 0.08,
  "04dp": 0.09,
  "06dp": 0.11,
  "08dp": 0.12,
  "12dp": 0.14,
  "16dp": 0.15,
  "24dp": 0.16,
  "soft": 0.87,
  "disabled": 0.38,
);

@mixin colors($background, $foreground, $primary, $accent) {
  --background: #{$background};
  --foreground: #{$foreground};
  --primary: #{$primary};
  --accent: #{$accent};

  @each $dp, $op in $elevations {
    --color-#{$dp}: #{rgba($foreground, $op)};
  }

  .dimmer {
    background-color: rgba($background, 0.9);
  }
}

body {
  @media (prefers-color-scheme: light) {
    @include colors(
      rgb(240, 252, 255),
      rgb(0, 18, 50),
      rgb(0, 94, 255),
      rgb(255, 36, 136)
    );
  }

  @media (prefers-color-scheme: dark) {
    @include colors(
      rgb(18, 18, 18),
      rgb(255, 255, 255),
      rgb(0, 166, 255),
      rgb(255, 36, 136)
    );
  }
}

body {
  background-color: var(--background);
}

* {
  color: var(--foreground);
}

@each $dp, $op in $elevations {
  .has-background-#{$dp} {
    background-color: var(--color-#{$dp});
  }
}

// TYPOGRAPHY
h1 {
  font-size: 32px;
  line-height: 2.1;
  margin: 12px 0;
}

h2 {
  font-size: 25px;
  line-height: 1.8;
  color: var(--color-soft);
  margin: 12px 0;
}

p,
a,
button {
  font-size: 16px;
  line-height: 1.2;
}

p {
  margin-bottom: 12px;
}

a:not(.button),
button.link {
  display: inline-block;
  text-decoration: none;
  color: var(--primary);
  background-color: transparent;
  outline: none;
  border: none;
  cursor: pointer;

  &:hover {
    text-decoration: underline;
  }
}

button:not(.link),
a.button {
  display: inline-block;
  text-decoration: none;
  background-color: var(--color-24dp);
  color: var(--primary);
  font-weight: bold;
  padding: 0 24px;
  cursor: pointer;
  border: none;
  height: 24px;
  line-height: 24px;
  border-radius: 12px;
  margin: 3px 0;
  text-transform: uppercase;
}

hr {
  border-color: var(--primary);
  border-width: 0.5px;
}

// COMPONENTS
.container {
  padding: 12px;
  max-width: 600px;
  width: 100%;
  margin: 12px auto;
}

.box {
  @extend .has-background-06dp;
  border-radius: 3px;

  & > .content {
    padding: 12px;
  }

  & > .field {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 12px 0;
    margin: 0 12px;

    &.clickable {
      cursor: pointer;
      padding: 12px;
      border-radius: 3px;

      label {
        cursor: pointer;
      }

      &:hover {
        background-color: var(--background);
      }
    }

    &:not(:last-child) {
      border-bottom: var(--color-08dp) 1px solid;
    }

    & > label {
      i {
        width: 32px;
      }

      flex-grow: 1;
    }

    & > input,
    & > select,
    & > .input {
      @extend .has-background-06dp;
      outline: none;
      border: none;
      padding: 6px 12px;
      border-radius: 3px;
    }
  }
}

input[type="checkbox"] {
  display: none;

  & + span.checkbox {
    width: 48px;
    height: 24px;
    border-radius: 12px;
    background-color: var(--background);
    position: relative;
    display: flex;
    cursor: pointer;

    &:before {
      content: "";
      height: 18px;
      width: 18px;
      border-radius: 50%;
      display: block;
      top: 3px;
      left: 3px;
      position: absolute;
      background-color: var(--foreground);
      transition-property: left;
      transition-duration: 0.3s;
    }
  }

  &:checked + span.checkbox {
    background-color: var(--primary);

    &:before {
      left: 27px;
    }
  }
}

.dimmer {
  &:not(.active) {
    display: none;
    background-color: transparent;
  }

  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 900;
}

.loader {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.keyboard {
  display: flex;
  flex-direction: column;
  height: 100vh;

  .content {
    flex-grow: 1;
    flex-shrink: 0;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    .box {
      padding: 12px;
      overflow-y: auto;
      max-height: 30vh;
      max-width: 400px;
      width: 100%;

      &:not(:first-child) {
        margin-left: 24px;
      }

      & > label {
        display: block;
        font-size: 22px;
        padding-bottom: 12px;
      }

      & > input {
        display: block;
        width: 100%;
        background-color: var(--background);
        font-size: 22px;
        border: none;
      }
    }
  }

  .board {
    flex-grow: 0;
    flex-shrink: 0;
    height: 60vh;
    display: flex;
    padding: 12px;
    @extend .has-background-06dp;

    .layout {
      flex-grow: 1;
      flex-shrink: 0;
      padding-right: 12px;

      display: flex;
      flex-direction: column;

      & > div {
        display: flex;
        flex-direction: row;
        flex-grow: 1;
      }
    }

    .actions {
      flex-grow: 0;
      flex-shrink: 0;
      flex-basis: 100px;
      display: flex;
      flex-direction: column;
    }

    .key {
      @extend .has-background-12dp;
      margin: 2px;
      flex-grow: 1;
      flex-shrink: 1;
      flex-basis: 1px;
      display: flex;
      align-items: center;
      justify-content: center;

      &.disabled {
        color: var(--background);
        @extend .has-background-02dp;
      }

      &:not(.disabled) {
        &:active,
        &.active {
          background-color: var(--primary);
        }

        cursor: pointer;
      }
    }
  }
}

.applet {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.cards {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: -12px;

  & > .card {
    width: 100%;
    margin: 12px;
    --radius: 12px;
    border-radius: var(--radius);
    background-color: var(--color-02dp);
    flex-basis: 400px;
    flex-grow: 1;

    & > * {
      overflow: hidden;
    }

    & > :first-child {
      border-top-left-radius: var(--radius);
      border-top-right-radius: var(--radius);
    }

    & > :last-child {
      border-bottom-left-radius: var(--radius);
      border-bottom-right-radius: var(--radius);
    }

    .card-header-image {
      height: 300px;
      max-height: 40vh;
      overflow: hidden;
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
    }

    .card-content {
      padding: 24px;
    }

    .card-header {
      padding: 12px 24px 0 24px;

      & > * {
        margin: 0;
      }
    }
  }
}
